import { response } from "../model/response";
import api from "../services/auth.provider";
class InvestmentProvider {
	constructor() {
		this.base = "/api/investments/";
		this.base_plan = "/api/plans/";
		this.api = api;
	}
	/**
	 * Get user's investments
	 * @param {string} id user's id
	 */
	async getUserInvestment(id) {
		const res = await fetch(`${this.base}${id}`);
		const result = response.asListResponse(await res.json());
		return result;
	}

	/**
	 * New user investment
	 * @param {number} amount Amount to fund
	 * @param {string} currencyId currency id
	 * @param {string} fundTarget fund channel type id
	 * @param {string} percent plan percentage
	 * @param {object} cardModel object contain credit card detail
	 */

	async newInvestment(
		amount,
		currencyId,
		userId,
		fundTarget,
		percent,
		cardModel
	) {
		const res = await fetch(`${this.base}addnew`, {
			method: "POST",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json"
			},
			body: JSON.stringify({
				amount,
				currencyId,
				userId,
				cardModel,
				percent,
				fundTarget
			})
		});

		const result = response.asSingleResponse(await res.json());
		return result;
	}

	async updateBalance(id, currentBalance) {
		const res = await fetch(`${this.base}u/balance`, {
			method: "POST",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
				Authorization: `Bearer ${this.api.getToken()}`
			},
			body: JSON.stringify({
				id,
				currentBalance
			})
		});

		const result = response.asSingleResponse(await res.json());
		return result;
	}

	async updateInvestmentMade(id, investmentMade) {
		const res = await fetch(`${this.base}u/investment`, {
			method: "POST",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
				Authorization: `Bearer ${this.api.getToken()}`
			},
			body: JSON.stringify({
				id,
				investmentMade
			})
		});

		const result = response.asSingleResponse(await res.json());
		return result;
	}

	async ReInvestmen(id) {
		const res = await fetch(`${this.base}reinvest`, {
			method: "POST",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
				Authorization: `Bearer ${this.api.getToken()}`
			},
			body: JSON.stringify({
				id
			})
		});

		const result = response.asSingleResponse(await res.json());
		return result;
	}
	/**
	 * Get all available investment plan
	 */

	async getInvestmentPlans() {
		const res = await fetch(this.base_plan);
		const result = response.asListResponse(await res.json());
		return result;
	}
	/**
	 * Get single investment plan object
	 * @param {string} id investment plan id
	 */

	async getInvestmentPlan(id) {
		const res = await fetch(`${this.base_plan}${id}`);
		const result = response.asSingleResponse(await res.json());
		return result;
	}
	/**
	 * New user investment
	 * @param {string} title title of the plan
	 * @param {string} description description of the plan | null
	 * @param {number} min_amount minimum amount for the plan
	 * @param {number} max_amount minimum amount for the plan
	 * @param {number} percent ROI percentage for the plan
	 * @param {string} service investment service id
	 */

	async newInvestmentPlan(
		title,
		min_amount,
		max_amount,
		description,
		percent,
		service
	) {
		const res = await fetch(`${this.base_plan}addPlan`, {
			method: "POST",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
				Authorization: `Bearer ${this.api.getToken()}`
			},
			body: JSON.stringify({
				title,
				min_amount,
				max_amount,
				description,
				percent,
				service
			})
		});

		const result = response.asSingleResponse(await res.json());
		return result;
	}
	/**
	 * Update investment plan
	 * @param {string} id investment id
	 * @param {string} title investment title
	 * @param {number} min_amount minimum amount
	 * @param {number} max_amount maximum amount
	 * @param {string} description investment description
	 * @param {number} percent percent for plan
	 * @param {string} service investment service id
	 */
	async updateInvestmentPlan(
		id,
		title,
		min_amount,
		max_amount,
		description,
		percent,
		service
	) {
		const res = await fetch(`${this.base_plan}${id}/update`, {
			method: "PUT",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
				Authorization: `Bearer ${this.api.getToken()}`
			},
			body: JSON.stringify({
				title,
				min_amount,
				max_amount,
				description,
				percent,
				service
			})
		});

		const result = response.asSingleResponse(await res.json());
		return result;
	}
	/**
	 * Remove single investment plan
	 * @param {string} id investment plan id
	 */
	async removeSingleInvestmentPlan(id) {
		const res = await fetch(`${this.base_plan}${id}/remove`, {
			method: "DELETE",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
				Authorization: `Bearer ${this.api.getToken()}`
			}
		});
		const result = response.asSingleResponse(await res.json());
		return result;
	}
	/**
	 * Get total investment due for an increase
	 * @param {number} page page number
	 * @param {number} limit record per request
	 */
	async getInvestmentDueForIncrease(page, limit) {
		const res = await fetch(
			`${this.base}due/increase?page=${page}&limit=${limit}`,
			{
				method: "GET",
				headers: {
					Accept: "application/json",
					"Content-Type": "application/json",
					Authorization: `Bearer ${this.api.getToken()}`
				}
			}
		);
		const result = response.asListResponse(await res.json());
		return result;
	}
	/**
	 * Get total investment due for an increase
	 * @param {number} page page number
	 * @param {number} limit record per request
	 */
	async getTrialAccounts(page, limit) {
		const res = await fetch(
			`${this.base}trail/account?page=${page}&limit=${limit}`,
			{
				method: "GET",
				headers: {
					Accept: "application/json",
					"Content-Type": "application/json",
					Authorization: `Bearer ${this.api.getToken()}`
				}
			}
		);
		const result = response.asListResponse(await res.json());
		return result;
	}
	/**
	 * Increase user investment
	 * @param {string} id investment id
	 * @param {number} amount amount to top up
	 */
	async IncreaseInvestment(id, amount) {
		const res = await fetch(`${this.base}credit/account`, {
			method: "PUT",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
				Authorization: `Bearer ${this.api.getToken()}`
			},
			body: JSON.stringify({ id, amount })
		});
		const result = response.asSingleResponse(await res.json());
		return result;
	}
	/**
	 * Close user investment
	 * @param {string} id investment id
	 */
	async closeUserInvestment(id) {
		const res = await fetch(`${this.base}${id}/close`, {
			method: "PUT",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
				Authorization: `Bearer ${this.api.getToken()}`
			}
		});
		const result = response.asSingleResponse(await res.json());
		return result;
	}
	/**
	 * Fund Target
	 */
	async GetFundPaymentTarget() {
		const res = await fetch(`${this.base}fundpayment/all`, {
			method: "GET"
		});
		const result = response.asListResponse(await res.json());
		return result;
	}

	/**
	 * CREDIT CARDS
	 */
	async GetCreditCards() {
		const res = await fetch(`${this.base}card/all`, {
			method: "GET",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
				Authorization: `Bearer ${this.api.getToken()}`
			}
		});
		const result = response.asListResponse(await res.json());
		return result;
	}

	/**
	 * Investment service
	 */
	async NewService(name) {
		const res = await fetch(`${this.base_plan}service/newservice`, {
			method: "POST",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
				Authorization: `Bearer ${this.api.getToken()}`
			},
			body: JSON.stringify({ name })
		});
		const result = response.asSingleResponse(await res.json());
		return result;
	}

	async GetServices() {
		const res = await fetch(`${this.base_plan}service/getall`, {
			method: "GET",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json"
			}
		});

		const result = response.asListResponse(await res.json());
		return result;
	}

	async GetPlansByServices(service) {
		const res = await fetch(`${this.base_plan}service/${service}`, {
			method: "GET",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
				Authorization: `Bearer ${this.api.getToken()}`
			}
		});
		const result = response.asListResponse(await res.json());
		return result;
	}

	async UpdateService(id, name) {
		const res = await fetch(`${this.base_plan}service/updateservice`, {
			method: "PUT",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
				Authorization: `Bearer ${this.api.getToken()}`
			},
			body: JSON.stringify({ id, name })
		});
		const result = response.asSingleResponse(await res.json());
		return result;
	}

	/**
	 * search user investment
	 * @param {string} keyword user email address
	 */
	async SearchInvestment(keyword) {
		const res = await fetch(`${this.base}user/search?keyword=${keyword}`, {
			method: "GET",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
				Authorization: `Bearer ${this.api.getToken()}`
			}
		});
		const result = response.asListResponse(await res.json());
		return result;
	}
}
const investmentProvider = new InvestmentProvider();
export default investmentProvider;
